










































































import HeadTtop from '../../../../components/head/head.vue'
export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '我的订单',
			backshow: true,
			topsrc: '../../../../static/images/back-w.png',
			order_id: '',
			details:[]
		}
	},
	onLoad(e) {
		this.order_id = e.order_id
		this.userinfo = uni.getStorageSync('userinfo')
		this.getData()
	},
	methods:{
		getData(){
			var that = this;
			let params = {
				order_id: this.order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(true, 'order/orderDetail', params, 'POST', res => {
				if (res.code == 200) {
					that.details = res.data
				}
			});
		},
		cancelOrder(order_id) { //取消订单
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/cancelOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout( res => {
								uni.navigateBack()
							},600)
						}
					});
				}
			});
		},
		confirmOrder(order_id){//确定收货
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/confirmOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout( res => {
								uni.navigateBack()
							},600)
						}
					});
				}
			});
		},
		finishOrder(order_id){ //完成订单
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/finishOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout( res => {
								uni.navigateBack()
							},600)
						}
					});
				}
			});
		},
		apppayOrder(order_id,allprice) { //订单支付
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
		
			if (that.userinfo.is_vip == 2) {
				uni.showActionSheet({
					itemList: ['微信支付', '支付宝支付'],
					success: function(res) {
						if (res.tapIndex === 0) {
							that.$http.HttpRequst.request(true, 'order/wxPay', params, 'POST', res => { //微信支付
								console.log(JSON.stringify(res.data))
								uni.requestPayment({
									provider: 'wxpay',
									orderInfo: res.data, //订单数据
									success: function(res) {
										uni.showToast({
											title: '支付成功',
											icon: 'success',
											duration: 800,
											success() {
												setTimeout(res => {
													that.getData()
												}, 800)
											}
										});
									},
									fail: function(err) {
										uni.showToast({
											title: '支付取消',
											icon: 'success',
											duration: 800
										});
									}
								});
							});
						}
						if (res.tapIndex === 1) {
							that.$http.HttpRequst.request(true, 'alipay/pay', params, 'POST', res => { //支付宝支付
								uni.requestPayment({
									provider: 'alipay',
									orderInfo: res, //订单数据
									success: function(res) {
										uni.showToast({
											title: '支付成功',
											icon: 'success',
											duration: 800,
											success() {
												setTimeout(res => {
													that.getData()
												}, 800)
											}
										});
									},
									fail: function(err) {
										uni.showToast({
											title: '支付取消',
											icon: 'success',
											duration: 800
										});
									}
								});
							});
						}
					}
				});
			} else { // 余额支付
				uni.showActionSheet({
					itemList: ['余额支付'],
					success: function(res) {
						if( res.tapIndex == 0 ){
							if( uni.getStorageSync('userinfo').balance < Number(allprice) ){
								uni.showToast({
									title: '余额不足，请充值',
									icon: 'success',
									duration: 600,
									success() {
										setTimeout( res => {
											uni.redirectTo({
												url: '/pages/info/interests/interests'
											})
										},600)
									}
								});
							}else{
								that.$http.HttpRequst.request(false, 'order/balancePay', params, 'POST', res => {
									if( res.code == 200 ){
										uni.showToast({
											title: '成功',
											icon: 'success',
											duration: 600,
											success() {
												setTimeout( res => {
													that.getData()
												},800)
											}
										});
									}
								});
							}
						}
					}
				});
			}
		
		
		},
	}
}
